<template>
  <div class="col-9 bg-muted d-flex flex-column">
    <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
    <div class="d-flex flex-row-reverse">
      <button class="m-4 btn btn-primary" @click="showAddModal()" round>
        <i class="las la-plus-circle"></i>Add Product Category
      </button>
    </div>
    <div class="row">
      <div class="col-6 p-2" v-for="pCategory in pCategoryAndProductList" :key="pCategory.ID">
        <div class="tag-container px-2 py-3">
          <div>
            <span @click="showEditModal(pCategory)" class="tag px-3 py-2">{{pCategory.Name}}</span>
          </div>
          <div class="mt-3">
            <span class="stag px-2 py-1" @click="addProduct(pCategory)">+ Add</span>
            <span
              v-for="product in pCategory.Products"
              @click="showActionsModal(product,pCategory)"
              class="stag px-2 py-1 m-1"
              :key="product.ID"
            >{{product.Name}}</span>
          </div>
        </div>
      </div>
    </div>
    </loading-overlay>
    <product-modal
      @addProduct="appendProduct"
      @updateProduct="editProduct"
      @deleteProduct="removeProduct"
    />
    <modal
      name="productCategoryModal"
      height="auto"
      width="400"
      @before-open="beforeOpen"
      @opened="focus"
    >
      <div class="form">
        <label>Product Category Name</label>
        <input
          placeholder=" Enter Product Category Name"
          type="text"
          v-model="pCategoryName"
          ref="pCategoryName"
          class="textbox form-control"
        />
        <div class="d-flex flex-row-reverse justify-content-between align-items-center">
          <button
            class="m-2 btn btn-primary"
            v-if="btnpCategory == 'Update'"
            @click="updateProductCategory"
          >Update</button>
          <a
            v-if="btnpCategory == 'Update'"
            @click="removeProductCategory"
            class="text-danger mr-2"
          >
            <i class="lar la-trash-alt"></i> Delete
          </a>
          <button
            class="m-2 btn btn-primary"
            v-if="btnpCategory == 'Add'"
            @click="addProductCategory"
          >Add</button>
        </div>
        <div class="clearfix"></div>
      </div>
    </modal>
  </div>
</template>
<script>
import axios from "axios";
import ProductModal from "./Product/ProductModal";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    ProductModal,
    LoadingOverlay
  },
  data() {
    return {
      product: "",
      pCategoryName: "",
      productName: "",
      pCategoryAndProductList: [],
      productList: [],
      pCategoryID: 0,
      productID: 0,
      btnpCategory: "",
      btnproduct: "",
      selected: "",
      btnAdd: false,
      parentTag: {},
      loadingOverlay:false
    };
  },

  methods: {
    addProduct(pCategory) {
      this.$modal.show("product", {
        parentpCategory: pCategory
      });
    },

    showActionsModal(product, pCategory) {
      this.$modal.show("product", {
        parentpCategory: pCategory,
        product: product
      });
    },

    showAddModal() {
      this.pCategoryName = "";
      this.btnpCategory = "Add";
      this.$modal.show("productCategoryModal");
    },
    showEditModal(pCategory) {
      this.pCategoryName = pCategory.Name;
      this.pCategoryID = pCategory.ID;
      this.btnpCategory = "Update";
      this.$modal.show("productCategoryModal");
    },

    add() {},

    async getpCategoryAndProduct() {
      try {
        // let loader = this.$loading.show({
        //   loader: "dots"
        // });
        this.loadingOverlay=true;
        const res = await axios
          .get("pcategorysandproducts")
          .then(res => {
            this.pCategoryAndProductList = res.data;
          })
          .finally(() => {
            this.loadingOverlay=false;
           // loader.hide();
          });
      } catch {
        return null;
      }
    },

    async addProductCategory() {
      if (!this.pCategoryName) return;
      if (this.exists) return;
      try {
        const res = await axios.post("addpCategory", {
          Name: this.pCategoryName
        });
        this.$modal.hide("productCategoryModal");
        this.getpCategoryAndProduct();
      } catch {
        return null;
      }
    },

    async updateProductCategory() {
      if (!this.pCategoryName) return;
      if (this.exists) return;
      try {
        const res = await axios.post("updatepCategory", {
          ID: this.pCategoryID,
          Name: this.pCategoryName
        });
        this.$modal.hide("productCategoryModal");
        this.getpCategoryAndProduct();
      } catch {
        return null;
      }
    },

    async removeProductCategory() {
      try {
        const res = await axios.post("removepCategory", {
          ID: this.pCategoryID
        });
        this.$modal.hide("productCategoryModal");
        this.getpCategoryAndProduct();
      } catch {
        return null;
      }
    },

    focus(modal) {
      if (modal == "productCategoryModal") {
        this.$refs.pCategoryName.focus();
      }
    },

    appendProduct(product) {
      this.pCategoryAndProductList.forEach(x => {
        if (x.ID === product.ProductCategoryID) {
          x.Products.push(product);
        }
      });
    },

    editProduct(product) {
      let index = this.pCategoryAndProductList.findIndex(
        x => x.ID == product.ProductCategoryID
      );
      let subItem = this.pCategoryAndProductList[index].Products.filter(
        x => x.ID == product.ID
      );
      subItem[0].Name = product.Name;
    },

    removepCategory(pCategory) {
      let index = this.pCategoryAndProductList.findIndex(
        x => x.ID == pCategoryID
      );
      this.pCategoryAndProductList.splice(index, 1);
    },

    removeProduct(product) {
      let index = this.pCategoryAndProductList.findIndex(
        x => x.ID == product.ProductCategoryID
      );
      let i = this.pCategoryAndProductList[index].Products.findIndex(
        x => x.ID == product.ID
      );
      this.pCategoryAndProductList[index].Products.splice(i, 1);
    }
  },
  mounted() {
    this.getpCategoryAndProduct();
  },
  computed: {
    exists() {
      return this.pCategoryAndProductList
        .map(x => x.Name)
        .includes(this.pCategoryName);
    }
  }
};
</script>
<style lang="scss" scoped>
@import "~bootstrap/scss/bootstrap.scss";
.subtag {
  display: flex;
  justify-content: center;
  padding: 5px;
  width: 25%;
  height: 50px;
  margin: 10px;
  font-size: 20px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.7em;
  color: $secondary;
  font-weight: 300;
  margin-top: 10px;
}
.button {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 30%;
  padding-top: 20px;
  padding-left: 80%;
}
.form {
  padding: 30px;
  margin: 10px;
}
.textbox {
  padding: 0;
  margin-bottom: 10px;
  display: block;
}
label {
  margin-bottom: 0px;
  color: #385898;
  font-weight: bold;
  font-size: 15px;
  text-align: left;
}
.confirm {
  float: right;
  padding-top: 15px;
  padding-right: 0;
  padding-bottom: 30px;
}
.v--modal-box {
  margin-top: 10% !important;
}
.style-chooser .vs__search::placeholder,
.style-chooser .vs__dropdown-toggle,
.style-chooser .vs__dropdown-menu {
  max-height: 100px;
}

.custom-avatar.avatar table[data-v-4ffd1741] {
  width: 40px;
  height: 40px;
}
</style>
<style lang="scss">
.tag-container {
  border-radius: 18px;
  background-color: #fff;
}

.tag {
  background-color: #e2e2e2;
  border-radius: 18px;
  font-weight: 600;
  font-size: 0.9em;
  letter-spacing: 0.5px;
}
.stag {
  cursor: pointer;
  border: 1px solid #e2e2e2;
  font-size: 0.8em;
  border-radius: 18px;
  letter-spacing: 0.5px;
  font-weight: 600;
  display: inline-block;
  transition: 0.3s;
  &:hover {
    background-color: #e2e2e2;
  }
  &:active {
    background-color: #888;
  }
  &:first-child {
    color: var(--primary);
    &:hover {
      color: #fff;
      background-color: var(--primary);
      border-color: var(--primary);
    }
  }
}

.bg-muted {
  background-color: #eeeded;
}
</style>
