<template>
  <modal name="product" height="auto" width="400" @before-open="beforeOpen" @opened="focus">
    <loading-overlay white :loading="loading">
      <div class="p-2">
        <template>
          <div class="mb-2">
            <span class="tag px-3 py-2">{{parentpCategory.Name}}</span>
          </div>
          <small class="text-muted">Product Name</small>
          <input ref="input" class="form-control" v-model="input" />
          <div class="d-flex flex-row-reverse justify-content-between align-items-center">
            <button v-if="editModal" class="btn btn-primary my-2 btn-sm" @click="update">Update</button>

            <a v-if="editModal" @click="remove" class="text-danger mr-2">
              <i class="lar la-trash-alt"></i> Delete
            </a>
            <button v-else class="btn btn-primary my-2 btn-sm" @click="add">Add</button>
          </div>
        </template>
      </div>
    </loading-overlay>
  </modal>
</template>
<script>
import axios from "axios";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    LoadingOverlay
  },
  data() {
    return {
      input: "",
      parentpCategory: {},
      product: {},
      loading: false
    };
  },
  methods: {
    resetModal() {
      this.input = "";
      this.product = {};
    },
    beforeOpen(event) {
      this.resetModal();

      let { parentpCategory, editModal, product } = event.params;
      this.parentpCategory = parentpCategory;
      if (editModal) this.editModal = editModal;
      if (product) {
        this.product = product;
        this.input = product.Name;
      }
    },
    focus() {
      this.$refs.input.focus();
    },
    update() {
      if (!this.input) return;
      if (this.exists) return;
      this.loading = true;

      axios
        .post("updateProduct", {
          ID: this.product.ID,
          Name: this.input,
          ProductCategoryID: this.parentpCategory.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("updateProduct", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("product");
        });
    },
    remove() {
      this.loading = true;

      axios
        .post("removeProduct", {
          ID: this.product.ID,
          Name: this.input,
          ProductCategoryID: this.parentpCategory.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("deleteProduct", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("product");
        });
    },
    add() {
      if (!this.input) return;
      if (this.exists) return;

      this.loading = true;
      axios
        .post("addProduct", {
          Name: this.input,
          ProductCategoryID: this.parentpCategory.ID
        })
        .then(res => {
          if (res.status == 200) {
            this.$emit("addProduct", res.data);
          }
        })
        .catch()
        .finally(res => {
          this.loading = false;
          this.$modal.hide("product");
        });
    }
  },
  computed: {
    exists() {
      return this.parentpCategory.Products.map(x => x.Name).includes(
        this.input
      );
    },
    editModal() {
      return this.product.ID;
    }
  }
};
</script>
